import axios from "axios";
import { useState } from "react";
import { productItemStyle, productItemNameStyle, productItemPriceStyle } from "./style";
import Modal from 'react-modal';
import { apiClient } from "../../../api/client";

Modal.setAppElement('#root');
const ProductsItem = ({ product }) => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [quantity, setQuantity] = useState(1);
  const [address, setAddress] = useState("");

  const handleBuyClick = async () => {
    const order = {
      products: [
        {
          productId: product.productId,
          quantity: quantity
        }
      ],
      address: address
    };

    try {
      const response = await apiClient.post("/api/v1/orders", order, {
        headers: {
          "Authorization": `${localStorage.getItem("Authorization")}`
        },
      });
      setModalIsOpen(false);
    } catch (error) {
      console.error("Failed to place order:", error);
    }
  };

  return (
    <div css={productItemStyle}>
      <h2 css={productItemNameStyle}>{product.productId}</h2>
      <h2 css={productItemNameStyle}>{product.productName}</h2>
      <p css={productItemPriceStyle}>{product.productPrice}</p>
      <button onClick={() => setModalIsOpen(true)}></button>
      <Modal isOpen={modalIsOpen} onRequestClose={() => setModalIsOpen(false)}>
        <h2>{product.productName}</h2>
        <p>{product.productPrice}</p>
        <a>  </a>
        <input type="number" value={quantity} onChange={(e) => setQuantity(e.target.value)} placeholder="" />
        <br />
        <a>  </a>
        <input type="text" value={address} onChange={(e) => setAddress(e.target.value)} placeholder="" />
        <br />
        <button onClick={handleBuyClick}></button>
        <button onClick={() => setModalIsOpen(false)}></button>
      </Modal>
    </div>
  );
};

export default ProductsItem;